import { AggregateId } from '../../aggregate-id';
import { DomainEvent } from '../../event/domain-event';

export abstract class AggregateEvent<T extends AggregateId> {

	protected constructor(
		private readonly aggregateId: T,
		private readonly type: string) {
	}

	abstract toDomainEvent(): DomainEvent<T>;

	getAggregateId(): AggregateId {
		return this.aggregateId;
	}

	getType(): string {
		return this.type;
	}

	equals(event: AggregateEvent<T>): boolean {
		return this.equalsByType(event) &&
			this.getAggregateId().equals(event.getAggregateId());
	}

	equalsByType(event: AggregateEvent<T>): boolean {
		return this.getType() === event.getType();
	}

}
