import { OnDestroy } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { filter, first, map, takeUntil } from 'rxjs/operators';

import { Command } from './command';
import { CommandDispatcher } from './command.dispatcher';

import { DomainEvent } from '../event/domain-event';
import { DomainEventBus } from '../event/domain-event.bus';
import { StatusResponse } from '../event/status/status.response';
import { AggregateId } from '../aggregate-id';


export abstract class ReplayCommandDispatcher<I extends AggregateId> implements OnDestroy {

	private unsubscribe$ = new Subject();

	private subscriptions: Array<number> = [];

	protected constructor(private readonly dispatcher: CommandDispatcher,
						  private readonly bus: DomainEventBus) {
	}

	protected abstract mapEventToResponse(event: DomainEvent<I>): StatusResponse;

	dispatch(command: Command): string {

		this.dispatcher.dispatch(command);

		return command.getMessageId();
	}

	dispatchAndWait(command: Command): Observable<StatusResponse> {

		const response$ =
			this.bus
				.pipe(
					filter((event: DomainEvent<I>) => event.fromCommand(command)),
					first(),
					map((event: DomainEvent<I>) => {
						return this.mapEventToResponse(event);
					}),
					takeUntil(this.unsubscribe$)
				);

		const subscription = setTimeout(() => {
			this.dispatcher.dispatch(command);
		});

		this.subscriptions.push(subscription);

		return response$;
	}

	ngOnDestroy() {
		this.unsubscribe$.next();
		this.unsubscribe$.complete();

		this.subscriptions.forEach((handle: number) => {
			clearTimeout(handle);
		});
	}

}
