import { Injectable } from '@angular/core';
import { Observable, of, Subject, throwError } from 'rxjs';
import { filter, take } from 'rxjs/operators';

import { DomainEvent } from './domain-event';
import { AggregateId } from '../aggregate-id';


@Injectable()
export class DomainEventStore {

	private domainEvents: Array<DomainEvent<AggregateId>> = [];

	private domainEvents$: Subject<DomainEvent<AggregateId>> = new Subject<DomainEvent<AggregateId>>();

	next(event: DomainEvent<AggregateId>): void {
		this.domainEvents.push(event);
		this.domainEvents$.next(event);
	}

	findEventByType(eventType: string): DomainEvent<AggregateId> {

		const events: Array<DomainEvent<AggregateId>> = this.getEvents();

		return events.reverse()
					 .find((event: DomainEvent<AggregateId>) => {
						 return event.constructor.name === eventType;
					 });
	}

	/**
	 * First tries to event event in the history,
	 * than method waits for future occurrences of the event.
	 */
	waitForEvent(eventType: string): Observable<DomainEvent<AggregateId>> {

		// find in a history
		const event = this.findEventByType(eventType);

		if (event) {
			return of(event);
		}

		// wait for future occurrence
		return this.waitForNextEventOccurrence(eventType);
	}

	waitForNextEventOccurrence(eventType: string): Observable<DomainEvent<AggregateId>>;
	waitForNextEventOccurrence(event: DomainEvent<AggregateId>): Observable<DomainEvent<AggregateId>>;
	waitForNextEventOccurrence(arg: string | DomainEvent<AggregateId>): Observable<DomainEvent<AggregateId>> {

		let eventType: string;

		if (arg instanceof DomainEvent) {
			eventType = arg.constructor.name;
		} else if (typeof arg === 'string') {
			eventType = arg;
		} else {
			return throwError(new Error('Unsupported argument type.'));
		}

		return this.domainEvents$
				   .pipe(
					   filter((event) => event.constructor.name === eventType),
					   take(1)
				   );
	}

	private getEvents(): Array<DomainEvent<AggregateId>> {
		return this.domainEvents;
	}

}
