import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

import { DomainEvent } from './domain-event';
import { DomainEventStore } from './domain-event.store';
import { AggregateId } from '../aggregate-id';


@Injectable()
export class DomainEventStream extends Subject<DomainEvent<AggregateId>> {

	constructor(private eventStore: DomainEventStore) {
		super();
	}

	next(event: DomainEvent<AggregateId>): void {
		super.next(event);
		this.eventStore.next(event);
	}
}
