import { Message } from '../domain/message';

export abstract class Logger<M extends Message> {

	private domainName: string;

	abstract start(): void;

	abstract stop(): void;

	protected abstract print(message: M): void;

	setDomain(domainName: string): void {
		this.domainName = domainName;
	}

	log(message: M): void {
		if (this.shouldPrint(message)) {
			this.print(message);
		}
	}

	protected shouldPrint(message: M): boolean {

		if (!this.domainName) {
			return true;
		}

		const log: string = message.toString();

		return log.includes(this.domainName);
	}
}
