import { ReadModelRootId } from '../feature-api/read/read-model-root-id';

export abstract class AggregateId {

	private readonly uid: string;

	protected constructor(uid: string) {
		this.uid = uid;
	}

	abstract toReadModelRootId(): ReadModelRootId;

	toString(): string {
		return this.uid;
	}

	getId(): string {
		return this.uid;
	}

	equals(aggregateId: AggregateId): boolean {
		return this.uid === aggregateId.getId();
	}
}
