import { Injectable } from '@angular/core';
import { Observable, Operator } from 'rxjs';
import { filter } from 'rxjs/operators';

import { DomainEvent } from './domain-event';
import { DomainEventStream } from './domain-event.stream';
import { AggregateId } from '../aggregate-id';


@Injectable()
export class DomainEventBus<E = DomainEvent<AggregateId>> extends Observable<E> {

	constructor(eventStream?: DomainEventStream) {
		super();

		if (eventStream) {
			this.source = eventStream;
		}
	}

	lift<R>(operator: Operator<E, R>): Observable<R> {
		const observable = new DomainEventBus<R>();
		observable.source = this;
		observable.operator = operator;
		return observable;
	}

	ofEvent<E2 extends E>(...events: Array<typeof DomainEvent>): Observable<any> {
		return (this as DomainEventBus<any>)
			.pipe(
				filter((domainEvent: DomainEvent<AggregateId>) => {
					return events.some((event: typeof DomainEvent) => {
						const eventInstance = this.createEventInstance(event);
						return eventInstance.equalsByType(domainEvent);
					});
				})
			);
	}

	private createEventInstance(event: typeof DomainEvent): DomainEvent<AggregateId> {
		const args: Array<void> = [],
			argumentLength = event.constructor.length;

		args.fill(undefined, 0, argumentLength);

		return (new (event as any)(...args));
	}

}
