import { AggregateId } from '../../domain/aggregate-id';
import { AggregateRoot } from '../../domain/command/aggregate/aggregate-root';
import { Optional } from '../../common/optional';

export abstract class InMemoryStore<I extends AggregateId, S extends AggregateRoot<I>> {

	private readonly state: Map<string, S> = new Map();

	set(aggregate: S): void {
		this.state.set(aggregate.getId().toString(), aggregate);
	}

	setMany(aggregates: Array<S>): void {
		aggregates.forEach((aggregate: S) => {
			this.set(aggregate);
		});
	}

	get(aggregateId: I): Optional<S> {
		return Optional.of(this.state.get(aggregateId.toString()));
	}

	getAll(): Array<S> {
		return Array.from(this.state.values());
	}

	has(aggregateId: I): boolean {
		return this.state.has(aggregateId.toString());
	}

	delete(aggregateId: I): void {
		this.state.delete(aggregateId.toString());
	}

	clear(): void {
		this.state.clear();
	}

}
