import { AggregateRoot } from '../aggregate-root';
import { CreateAggregateCommandHandler } from './create-aggregate.command-handler';
import { AggregateFactoryArchive, AggregateRepositoryArchive } from '../../config/define';
import { AggregateFactory } from '../aggregate-factory';
import { AggregateRepository } from '../aggregate-repository';
import { AggregateId } from '../../../aggregate-id';
import { CreateAggregateCommand } from './create-aggregate.command';
import { DomainEventPublisher } from '../../../event/domain-event.publisher';


export function createAggregateCommandHandlerFactory<I extends AggregateId, A extends AggregateRoot<I>, C extends CreateAggregateCommand>(createAggregateCommandHandler: CreateAggregateCommandHandler<A, C>, factoryArchive: AggregateFactoryArchive<I, A>, aggregateRepositoryArchive: AggregateRepositoryArchive<I, A>, aggregateName: string, domainEventPublisher: DomainEventPublisher): CreateAggregateCommandHandlerImpl<I, A, C> {
	return new CreateAggregateCommandHandlerImpl<I, A, C>(createAggregateCommandHandler, factoryArchive, aggregateRepositoryArchive, aggregateName, domainEventPublisher);
}

export class CreateAggregateCommandHandlerImpl<I extends AggregateId, A extends AggregateRoot<I>, C extends CreateAggregateCommand> {

	private readonly commandType: string;

	constructor(private readonly createAggregateCommandHandler: CreateAggregateCommandHandler<A, C>,
				private readonly aggregateFactoryArchive: AggregateFactoryArchive<I, A>,
				private readonly aggregateRepositoryArchive: AggregateRepositoryArchive<I, A>,
				private readonly aggregateType: string,
				private readonly domainEventPublisher: DomainEventPublisher) {
		this.commandType = this.createCommandInstance().getMessageType();
	}

	handleCommand(command: C) {

		const aggregateId = command.getAggregateId() as I;

		const optFactory = this.aggregateFactoryArchive.get(this.aggregateType);

		optFactory.ifPresent((factory: AggregateFactory<I, A>) => {

			const aggregate = factory.create(aggregateId);

			const type = aggregate.getType(),
				createCommandConstructor = aggregate.createEvent();

			const createCommand = new createCommandConstructor(aggregateId, type);

			aggregate.addEvent(createCommand);

			const optRepository = this.aggregateRepositoryArchive.get(this.aggregateType);

			optRepository.ifPresent((repo: AggregateRepository<I, A>) => {

				repo.save(aggregate);

				this.domainEventPublisher.publishFromAggregate(aggregate);
			});
		});

	}

	forCommand(command: C): boolean {
		return this.commandType === command.getMessageType();
	}

	private createCommandInstance(): C {
		const args: Array<void> = [],
			argumentLength = this.createAggregateCommandHandler.forCommand().constructor.length;

		args.fill(undefined, 0, argumentLength);

		return (new (this.createAggregateCommandHandler.forCommand() as any)(...args));
	}

}
