import { PersistAnemia } from './persist-anemia';
import { Optional } from '../../common/optional';
import { ReadModelRootId } from '../../feature-api/read/read-model-root-id';
import { AggregateId } from '../../domain/aggregate-id';


export abstract class PersistStateStore<I extends ReadModelRootId, A extends PersistAnemia<I>> {

	private readonly state: Map<string, A> = new Map();

	set(anemia: A): void {
		this.state.set(anemia.getId(), anemia);
	}

	setMany(anemias: Array<A>): void {
		anemias.forEach((anemia: A) => {
			this.set(anemia);
		});
	}

	get(aggregateId: AggregateId): Optional<A> {
		return Optional.of(this.state.get(aggregateId.toString()));
	}

	getAll(): Array<A> {
		return Array.from(this.state.values());
	}

	clear(): void {
		this.state.clear();
	}
}
