import { Type } from '@angular/core';
import { Observable } from 'rxjs';
import { filter, tap } from 'rxjs/operators';

import { AggregateId } from '../../domain/aggregate-id';
import { DomainEvent } from '../../domain/event/domain-event';
import { ReadModelRootId } from '../read/read-model-root-id';
import { DomainEventBus } from '../../domain/event/domain-event.bus';

export abstract class EventRepository<R extends ReadModelRootId, I extends AggregateId> {

	protected constructor(private readonly domainEventBus: DomainEventBus) {}

	protected onEvent(aggregateId: R, eventType: Type<DomainEvent<I>>): Observable<DomainEvent<I>> {
		return this.domainEventBus
				   .ofEvent(
					   eventType as any
				   )
				   .pipe(
					   filter((event: DomainEvent<AggregateId>) => event.getAggregateId().toString() === aggregateId.toString())
				   );
	}
}
