import { AggregateId } from '../aggregate-id';
import { Command } from '../command/command';
import { DomainEventPayload } from './domain-event.payload';
import { Message } from '../message';


export abstract class DomainEvent<I extends AggregateId> extends Message {

	private requestCommandId: string;

	private payload: DomainEventPayload;

	protected constructor(aggregateId: I,
						  messageType: string,
						  payload?: any) {
		super(aggregateId, messageType);
		this.payload = payload;
	}

	isSameType(event: DomainEvent<I>): boolean {
		return this.constructor.name === event.constructor.name;
	}

	setRequestCommand(command: Command): void {
		this.requestCommandId = command.getMessageId();
	}

	fromCommand(command: Command): boolean {
		return command.getMessageId() === this.requestCommandId;
	}

	setPayload(payload: DomainEventPayload): void {
		this.payload = payload;
	}

	getPayload(): any {
		return this.payload;
	}
}
