import { AggregateId } from '../../../domain/aggregate-id';

import { AggregateRoot } from '../../../domain/command/aggregate/aggregate-root';
import { AggregateStore } from '../../../domain/command/store/aggregate.store';
import { AggregateStoreRegister } from '../../../domain/command/store/aggregate-store.register';

import { InMemoryStore } from '../in-memory.store';

import { Optional } from '../../../common/optional';

export abstract class InMemoryAggregateStore<I extends AggregateId, T extends AggregateRoot<I>> extends AggregateStore<I, T> {

	protected constructor(private readonly inMemoryStore: InMemoryStore<I, T>,
						  private readonly aggregateStoreRegister: AggregateStoreRegister) {
		super();
		this.aggregateStoreRegister.register(this);
	}

	save(aggregate: T): void;
	save(aggregates: Array<T>): void;
	save(arg: T | Array<T>): void {

		if (Array.isArray(arg)) {
			arg.forEach((aggregate: T) => {
				this.inMemoryStore.set(aggregate);
			});
		} else {
			const aggregate = arg;

			this.inMemoryStore.set(aggregate);
		}
	}

	getById(aggregateId: I): Optional<T> {

		const optAggregate = this.inMemoryStore.get(aggregateId);

		optAggregate.ifPresent((a) => a.clearEvents());

		return optAggregate;
	}

	getAll(): Array<T> {
		return this.inMemoryStore.getAll();
	}

	remove(aggregateId: I): void {
		this.inMemoryStore.delete(aggregateId);
	}

}
