import { Injectable, OnDestroy } from '@angular/core';
import { Subject } from 'rxjs';
import { filter, takeUntil } from 'rxjs/operators';

import { DomainEventLogger } from '../../../domain/event/domain-event.logger';
import { DomainEventBus } from '../../../domain/event/domain-event.bus';
import { DomainEvent } from '../../../domain/event/domain-event';
import { AggregateStoreRegister } from '../../../domain/command/store/aggregate-store.register';
import { AggregateId } from '../../../domain/aggregate-id';


@Injectable()
export class ConsoleEventLogger extends DomainEventLogger implements OnDestroy {

	private enabled = false;

	private unsubscribe$ = new Subject();

	constructor(eventBus: DomainEventBus,
				private readonly aggregateStoreRegister: AggregateStoreRegister) {
		super();

		eventBus
			.pipe(
				filter(() => this.enabled),
				takeUntil(this.unsubscribe$)
			)
			.subscribe((domainEvent: DomainEvent<AggregateId>) => {
				this.log(domainEvent);
			});
	}

	ngOnDestroy() {
		this.unsubscribe$.next();
		this.unsubscribe$.complete();
	}

	start(): void {
		this.enabled = true;
	}

	stop(): void {
		this.enabled = false;
	}

	protected print(domainEvent: DomainEvent<AggregateId>): void {
		const aggregateId = domainEvent.getAggregateId(),
			aggregates = this.aggregateStoreRegister.captureAggregatesSnapshot(aggregateId);

		console.log(domainEvent.toString(), domainEvent, aggregates);
	}

}
