import { AggregateId } from './aggregate-id';

import { RandomStringGenerator } from '../common/random-string.generator';

export abstract class Message {

	protected constructor(private readonly aggregateId: AggregateId,
						  protected readonly messageType: string,
						  private readonly messageId: string = RandomStringGenerator.generate()) {
	}

	getMessageType(): string {
		return this.messageType;
	}

	getAggregateId(): AggregateId {
		return this.aggregateId;
	}

	getMessageId(): string {
		return this.messageId;
	}

	toString(): string {
		return this.messageType;
	}

	equalsByType(message: Message): boolean {
		return this.getMessageType() === message.getMessageType();
	}

	equals(message: Message): boolean {
		return (this.getMessageType() === message.getMessageType()) && (this.messageId === message.messageId);
	}

	ofMessageType(messageType: string): boolean;
	ofMessageType(messageTypes: Array<string>): boolean;
	ofMessageType(arg: string | Array<string>): boolean {

		if (Array.isArray(arg)) {

			let found = arg.find((messageType: string) => this.isMessageType(messageType));

			return !!found;

		} else {
			return this.isMessageType(arg);
		}
	}

	private isMessageType(messageType: string): boolean {
		return this.getMessageType() === messageType;
	}
}
