import { BehaviorSubject, Observable, ReplaySubject, Subject } from 'rxjs';
import { distinctUntilChanged, map } from 'rxjs/operators';

import { AggregateId } from '../domain/aggregate-id';
import { ReactiveService } from './reactive.service';

export interface DefaultAggregateValues<T> {
	aggregateId: AggregateId;
	value: T;
}

export abstract class AggregateArchive<T> extends ReactiveService {

	private archive = new Map<string, T>();

	private archive$: Subject<Map<string, T>>;

	protected constructor(defaultValue?: DefaultAggregateValues<T>) {
		super();
		this.initArchive(defaultValue);
	}

	when(aggregateId: AggregateId): Observable<T> {
		return this.archive$
				   .asObservable()
				   .pipe(
					   map((map: Map<string, T>) => {
						   return map.get(aggregateId.toString());
					   }),
					   distinctUntilChanged()
				   );
	}

	next(aggregateId: AggregateId, value: T): void {
		this.archive.set(aggregateId.toString(), value);
		this.archive$.next(this.archive);
	}

	private initArchive(defaultValue?: DefaultAggregateValues<T>): void {
		if (defaultValue) {
			this.archive.set(defaultValue.aggregateId.toString(), defaultValue.value);
			this.archive$ = new BehaviorSubject<Map<string, T>>(this.archive);
		} else {
			this.archive$ = new ReplaySubject<Map<string, T>>(1);
		}
	}

}
