import { Inject, Injectable } from '@angular/core';
import { Observable, Operator, Subject } from 'rxjs';
import { filter } from 'rxjs/operators';

import { Command } from './command';
import { FILTERED_COMMAND_STREAM } from './filtered-command-stream';
import { AggregateRoot } from './aggregate/aggregate-root';
import { CreateAggregateCommandHandlerImpl } from './aggregate/create/create-aggregate.command-handler-impl';
import { CommandHandlerImpl } from './handler/command-handler-impl';
import { AggregateId } from '../aggregate-id';

@Injectable()
export class CommandBus<C = Command> extends Observable<C> {

	constructor(@Inject(FILTERED_COMMAND_STREAM) commandsStream?: Subject<Command>) {
		super();

		if (commandsStream) {
			this.source = commandsStream;
		}
	}

	lift<R>(operator: Operator<C, R>): Observable<R> {
		const observable = new CommandBus<R>();
		observable.source = this;
		observable.operator = operator;
		return observable;
	}

	ofCommand<C2 extends C>(...commandTypes: Array<string>): any {
		return (this as CommandBus<any>)
			.pipe(
				filter((command: Command) => {
					return commandTypes.some((commandType: string) => command.ofMessageType(commandType));
				})
			);
	}

	ofCommandHandler<C2 extends C>(...handlers: Array<CommandHandlerImpl<AggregateId, AggregateRoot<AggregateId>, Command>>): any {
		return (this as CommandBus<any>)
			.pipe(
				filter((command: Command) => {
					return handlers.some((handler: CommandHandlerImpl<AggregateId, AggregateRoot<AggregateId>, Command>) => handler.forCommand(command));
				})
			);
	}

	ofCreateAggregateHandler<C2 extends C>(...handlers: Array<CreateAggregateCommandHandlerImpl<any, AggregateRoot<AggregateId>, Command>>): any {
		return (this as CommandBus<any>)
			.pipe(
				filter((command: Command) => {
					return handlers.some((handler: CreateAggregateCommandHandlerImpl<any, AggregateRoot<AggregateId>, Command>) => handler.forCommand(command));
				})
			);
	}

	ofNullHandler<C2 extends C>(handlers: Array<CommandHandlerImpl<AggregateId, AggregateRoot<AggregateId>, Command>>, aggregateCommandHandlers: Array<CreateAggregateCommandHandlerImpl<AggregateId, AggregateRoot<AggregateId>, Command>>): any {
		return (this as CommandBus<any>)
			.pipe(
				filter((command: Command) => {

					if (!handlers) {
						return true;
					}

					return !handlers.some((handler: CommandHandlerImpl<AggregateId, AggregateRoot<AggregateId>, Command>) => handler.forCommand(command)) &&
						!aggregateCommandHandlers.some((handler: CreateAggregateCommandHandlerImpl<AggregateId, AggregateRoot<AggregateId>, Command>) => handler.forCommand(command));
				})
			);
	}

}
