import { AggregateRoot } from '../aggregate/aggregate-root';
import { AggregateStore } from './aggregate.store';
import { AggregateId } from '../../aggregate-id';

export class AggregateStoreRegister {

	private readonly stores: Array<AggregateStore<AggregateId, AggregateRoot<AggregateId>>> = [];

	register(store: AggregateStore<AggregateId, AggregateRoot<AggregateId>>): void {
		this.stores.push(store);
	}

	captureAggregatesSnapshot(aggregateId: AggregateId): { [key: string]: AggregateRoot<AggregateId> } {

		if (!aggregateId) {
			return {};
		}

		let aggregates = {};

		this.stores.forEach((store: AggregateStore<AggregateId, AggregateRoot<AggregateId>>) => {

			const aggregate = store.getById(aggregateId);

			if (aggregate) {

				let aggregateName = aggregate.constructor.name;

				aggregates[aggregateName] = aggregate;
			}

		});

		return this.cloneAggregates(aggregates);
	}

	private cloneAggregates(aggregates: { [key: string]: AggregateRoot<AggregateId> }): { [key: string]: AggregateRoot<AggregateId> } {
		return JSON.parse(JSON.stringify(aggregates));
	}
}
