import { Type } from '@angular/core';

import { AggregateEvent } from './aggregate-event';
import { AggregateId } from '../../aggregate-id';


export abstract class AggregateRoot<I extends AggregateId> {

	private readonly aggregateId: I;

	private readonly events: Array<AggregateEvent<I>>;

	protected constructor(aggregateId: I,
						  private readonly type: string) {
		this.aggregateId = aggregateId;
		this.events = [];
	}

	abstract createEvent(): Type<AggregateEvent<I>>;

	getId(): I {
		return this.aggregateId;
	}

	getType(): string {
		return this.type;
	}

	getEvents(): ReadonlyArray<AggregateEvent<I>> {
		return this.events as ReadonlyArray<AggregateEvent<I>>;
	}

	addEvent(event: AggregateEvent<I>): void;
	addEvent(events: Array<AggregateEvent<I>>): void;
	addEvent(args: AggregateEvent<I> | Array<AggregateEvent<I>>): void {

		if (Array.isArray(args)) {
			for (let event of args) {
				this.events.push(event);
			}
		} else {
			this.events.push(args as AggregateEvent<I>);
		}
	}

	clearEvents(): void {
		this.events.length = 0;
	}
}
