import { AggregateRoot } from '../aggregate/aggregate-root';
import { Command } from '../command';
import { AggregateRepositoryArchive } from '../config/define';
import { AggregateRepository } from '../aggregate/aggregate-repository';
import { CommandHandler } from './command.handler';
import { AggregateId } from '../../aggregate-id';


export function commandHandlerFactory<I extends AggregateId, A extends AggregateRoot<I>, C extends Command>(
	commandHandler: CommandHandler<A, C>,
	aggregateRepositoryArchive: AggregateRepositoryArchive<I, A>,
	aggregateName: string
): CommandHandlerImpl<I, A, C> {
	return new CommandHandlerImpl<I, A, C>(commandHandler, aggregateRepositoryArchive, aggregateName);
}

export class CommandHandlerImpl<I extends AggregateId, A extends AggregateRoot<I>, C extends Command> {

	private readonly commandType: string;

	constructor(private readonly commandHandler: CommandHandler<A, C>,
				private readonly aggregateRepositoryArchive: AggregateRepositoryArchive<I, A>,
				private readonly aggregateType: string) {
		this.commandType = this.createCommandInstance().getMessageType();
	}

	publishDomainEvents(aggregate: A, command: C): void {
		this.commandHandler.publishDomainEvents(aggregate, command);
	}

	handleCommand(command: C) {

		const aggregateId = command.getAggregateId() as I;

		const optRepository = this.aggregateRepositoryArchive.get(this.aggregateType);

		optRepository.ifPresent((repo: AggregateRepository<I, A>) => {

			const optAggregate = repo.getById(aggregateId);

			optAggregate.ifPresent((aggregate: A) => {

				this.commandHandler.handleAggregate(aggregate, command);

				this.publishDomainEvents(aggregate, command);
			});
		});
	}

	forCommand(command: C): boolean {
		return this.commandType === command.getMessageType();
	}

	private createCommandInstance(): C {
		const args: Array<void> = [],
			argumentLength = this.commandHandler.forCommand().constructor.length;

		args.fill(undefined, 0, argumentLength);

		return (new (this.commandHandler.forCommand() as any)(...args));
	}

}
