export class Optional<T> {

	static empty(): Optional<any> {
		return new Optional<any>(null);
	}

	static of<U>(value: U): Optional<U> {
		return new Optional<U>(value);
	}

	private static isEmpty(value: any): boolean {
		return typeof value === 'undefined' || value === null;
	}

	private readonly value: T;

	private constructor(value: T) {
		if (!Optional.isEmpty(value) && Optional.isEmpty(value)) {
			return Optional.empty();
		}
		this.value = value;
		return this;
	}

	isEmpty(): boolean {
		return Optional.isEmpty(this.value);
	}

	isPresent(): boolean {
		return !this.isEmpty();
	}

	filter(filterer: (value: T) => boolean): Optional<T> {
		if (this.isPresent() && filterer(this.value)) {
			return this;
		}
		return Optional.empty();
	}

	forEach(callback: (value: T) => any): void {
		if (this.isPresent()) {
			callback(this.value);
		}
	}

	map<U>(mapper: (value: T) => U): Optional<U> {
		if (this.isPresent()) {
			return new Optional<U>(mapper(this.value));
		}
		return Optional.empty();
	}

	/**
	 * @deprecated
	 */
	getValueOrNullOrThrowError(): T {
		return this.value;
	}

	getOrThrow(): T {
		if (this.isEmpty()) {
			throw new Error('Called getOrThrow on an empty Optional');
		}
		return this.value;
	}

	getOrElse<U extends T>(other: () => U): T {
		if (this.isPresent()) {
			return this.value;
		}
		return other();
	}

	ifPresent(method: (value: T) => void): void {
		if (this.isPresent()) {
			method(this.value);
		}
	}

	ifEmpty(method: () => void): void {
		if (this.isEmpty()) {
			method();
		}
	}

	orElse<U extends T>(other: () => Optional<U>): Optional<T> {
		if (this.isPresent()) {
			return this;
		}
		return other();
	}

}
