import { AggregateId } from '../../../domain/aggregate-id';
import { AggregateRoot } from '../../../domain/command/aggregate/aggregate-root';

import { ReadModelRoot } from '../../../feature-api/read/read-model-root';

import { ReadModelStore } from '../../../feature-api/read/store/read-model.store';

import { InMemoryStore } from '../in-memory.store';
import { Optional } from '../../../common/optional';
import { ReadModelRootId } from '../../../feature-api/read/read-model-root-id';


export abstract class InMemoryReadModelStore<I extends ReadModelRootId,
	R extends ReadModelRoot<I>,
	B extends AggregateId,
	A extends AggregateRoot<B>> extends ReadModelStore<I, R> {

	protected constructor(private readonly inMemoryStore: InMemoryStore<B, A>) {
		super();
	}

	abstract toReadModel(aggregate: A): R;

	getById(aggregateId: B): Optional<R> {

		// const aggregateId = readModelRootId.toAggregateId() as B; // TODO remove as

		return this.getValue(aggregateId);
	}

	getAll(): ReadonlyArray<R> {
		return this.inMemoryStore
				   .getAll()
				   .map((aggregate: A) => this.toReadModel(aggregate));
	}

	private getValue(aggregateId: B): Optional<R> {
		const optAggregate: Optional<A> = this.inMemoryStore.get(aggregateId);

		return optAggregate.map(this.toReadModel.bind(this));
	}

}
