import { ReadModelRootId } from '../../feature-api/read/read-model-root-id';

export abstract class PersistAnemia<I extends ReadModelRootId> {

	private readonly readModelRootId: I;

	protected constructor(uid: I) {
		this.readModelRootId = uid;
	}

	getReadModelRootId(): I {
		return this.readModelRootId;
	}

	getId(): string {
		return this.getReadModelRootId().toString();
	}

}
