import { DomainEventStatus } from './domain-event-status';
import { DomainEventPayload } from '../domain-event.payload';

export abstract class StatusResponse {

	protected constructor(private readonly status: DomainEventStatus,
						  private readonly payload?: DomainEventPayload) {
	}

	getStatus(): DomainEventStatus {
		return this.status;
	}

	getPayload(): DomainEventPayload {
		return this.payload;
	}
}
