import { StaticProvider, Type } from '@angular/core';

import { DOMAIN_EVENT_HANDLERS } from './event/domain-event-handlers';
import { DomainEventHandler } from './event/domain-event.handler';
import { AggregateId } from './aggregate-id';

export function provideEventHandlers<I extends AggregateId>(handlers: Array<Type<DomainEventHandler<I>>>): Array<StaticProvider> {
	return handlers.map((handler: Type<DomainEventHandler<I>>) => {
		return {
			provide: DOMAIN_EVENT_HANDLERS,
			useClass: handler,
			multi: true
		};
	}) as any;
}
