import { Injectable } from '@angular/core';
import { AggregateEvent } from '../command/aggregate/aggregate-event';

import { DomainEventStream } from './domain-event.stream';
import { DomainEvent } from './domain-event';
import { AggregateId } from '../aggregate-id';
import { AggregateRoot } from '../command/aggregate/aggregate-root';


@Injectable()
export class DomainEventPublisher {

	constructor(private eventStream: DomainEventStream) {
	}

	publish(event: DomainEvent<AggregateId>): void;
	publish(events: ReadonlyArray<DomainEvent<AggregateId>>): void;
	publish(args: DomainEvent<AggregateId> | ReadonlyArray<DomainEvent<AggregateId>>): void {

		if (Array.isArray(args)) {

			for (let arg of args) {
				this.publishEvent(arg);
			}
		} else if (args instanceof DomainEvent) {
			this.publishEvent(args);
		}
	}

	publishFromAggregate(aggregate: AggregateRoot<AggregateId>): void {
		aggregate.getEvents()
				 .forEach((aggregateEvent: AggregateEvent<AggregateId>) => {
					 this.publish(
						 aggregateEvent.toDomainEvent()
					 );
				 });
	}

	private publishEvent(event: DomainEvent<AggregateId>): void {

		if (!event) {
			console.error(`${event} is not defined`);
		}

		if (!(event instanceof DomainEvent)) {
			// throw new Error(`${event} is not a DomainEvent`);

			console.error(`${event} is not a DomainEvent`);
		}

		this.eventStream.next(event);
	}
}
