export class ReadModelEntityId {

	private readonly uid: string;

	protected constructor(uid: string) {
		this.uid = uid;
	}

	toString(): string {
		return this.uid;
	}

	getId(): string {
		return this.uid;
	}

	equals(entityId: ReadModelEntityId): boolean {
		return this.uid === entityId.getId();
	}
}
